import React, { useState, useEffect, useRef } from 'react';
import styled from '@emotion/styled';

import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

import Box from './Box'
import Chevron from './Chevron'
import useResponsive from '../contexts/mediaQuery/useResponsive';

const StyleBox = styled(Box)`
  img,
  picture {
    display: block;
  }
  .slick-track {
    display: flex;
    align-items: stretch;
  }
  .slick-arrow {
    height: auto;
    font-size: 0.75em;
    z-index: 1;
    background-color: silver;
    color: black;
    &::before {
      content: none;
    }
    :hover {
      background-color: silver;
      color: black;
    }
  }
  .slick-prev {
    left: ${props => props.isMobile ? (props.charts ? 0 : '-2.75em') : '-4.5em'};
    top: ${props => props.isMobile && props.charts && '1em'};
    width: ${props => props.isMobile ? '1.5rem' : '2.5rem'};
  }
  .slick-next {
    right: ${props => props.isMobile ? (props.charts ? 0 : '-2.75em') : '-4.5em'};
    top: ${props => props.isMobile && props.charts && '1em'};
    width: ${props => props.isMobile ? '1.5rem' : '2.5rem'};
  }
`

const Carousel = ({
    children,
    width,
    flex,
    autoplay,
    arrows,
    slidesToShow,
    silderProps,
    typeIndex,
    setType,
    charts,
    ...props
  }) => {
  // const [activeItemIndex, setActiveIndex] = useState(0)
  const slickRef = useRef()
  useEffect(() => {
    if (slickRef.current) {
      slickRef.current.slickGoTo(typeIndex)
    }
    // if (slickRef.current) {
    //   slickRef.current.slickGoTo(typeIndex)
    // }
  }, [slickRef, typeIndex])
  const { isMobile } = useResponsive()
  return (
    <StyleBox position="relative" charts={charts} isMobile={isMobile} width={width} flex={flex} {...props}>
      <Slider
        autoplay={autoplay}
        prevArrow={<Chevron.Left isMobile={isMobile} />}
        nextArrow={<Chevron.Right isMobile={isMobile} />}
        arrows={arrows}
        dots={false}
        ref={slickRef}
        autoplaySpeed={3500}
        speed={750}
        beforeChange={(from, to) => {
          if (typeof setType === 'function') {
            setType(to)
          }
        }}
        slidesToShow={slidesToShow || 1}
        {...silderProps}
      >
        {children}
      </Slider>
    </StyleBox>
  );
}

Carousel.defaultProps = {
  arrows: true,
}

export default Carousel;
