import React from 'react'
import Text from '../../../components/Text'
import Box from '../../../components/Box'
import Image from '../../../components/Image'
import PageModule from '../../../components/PageModule'
import { responsive } from '../../../components/ThemeProvider/theme'

import circle from '../circle.svg'
import useResponsive from '../../../contexts/mediaQuery/useResponsive'
import Carousel from '../../../components/Carousel'
import LeftTitle from '../../../components/LeftTitle'

const circleData = [
  {
    width: '24.3%',
    minWidth: '336px',
    top: '10.8%',
    right: '28%',
    titleText: (
      <>
        即使整體產值下滑<br/>
        會員購票能量<Text.Inline color="dance">依然不減</Text.Inline>
      </>
    ),
    description: [
      '節目票房少4.6億，\n較2019年下滑約34%',
      '會員購票人數近20萬，\n較2019年略增200人'
    ],
    mobileDescription: [
      '節目票房較2019年減少4.6億',
      '會員購票人數近20萬，\n較2019年略增200人'
    ],
  },
  {
    width: '18.4%',
    minWidth: '260px',
    top: '30.0%',
    right: '3.7%',
    titleText: (
        <>
          戲劇節目<br/>
          <Text.Inline color="dance">10月起</Text.Inline>有明顯成長
        </>
    ),
    description: [
      '10~12月較前兩年\n平均值成長近1億',
    ]
  },
  {
    width: '18.4%',
    minWidth: '260px',
    top: '59.3%',
    right: '18.5%',
    titleText: (
        <>
          藝FUN券振興效益<br/>
          帶來<Text.Inline color="dance">4472萬</Text.Inline>票房收入
        </>
    ),
    description: [
      '佔兩廳院售票平台\n同期票房5%',
    ]
  },
]

const CircleModule = ({ width, top, right, titleText, description, ...props}) => (
  <Box.Absolute width={width} top={top} right={right} {...props}>
    <Image src={circle} />
    <Box.AbsCenter width="100%" textAlign="center">
      <Text.H4 whiteSpace="pre-wrap">
        {titleText}
      </Text.H4>
      <Box.Inline>
        {description.map((d, k) => (
          <Text fontSize={responsive('0.75em', '1em')} mt="0.625rem" whiteSpace="pre-wrap" fontWeight="400" textAlign={responsive('center', "left")} key={k}>
            {d}
          </Text>
        ))}
      </Box.Inline>
    </Box.AbsCenter>
  </Box.Absolute>
)

const Page = () => {
  const { isMobile } = useResponsive()
  return (
    <PageModule
      left={
        <LeftTitle
          smallTilte="2020疫情之下"
          title="表演藝術市場能量重新綻放"
          description="在疫情影響之下，整體產值雖較2019年大幅下滑，但在疫情逐漸穩定後，觀眾的支持熱度恢復，成為表演藝術市場轉型前的契機。"
        />
      }
      right={
        <>
          {isMobile ? (
            <Carousel
              width="76%"
              mx="auto"
              slidesToShow={1}
            >
              {circleData.map(({ titleText, description, mobileDescription }, i) => (
                <CircleModule
                  titleText={titleText}
                  description={mobileDescription || description}
                  position="relative"
                  key={i}
                />
              ))}
            </Carousel>
          ) : circleData.map(({ width, top, right, titleText, description, minWidth }, i) => (
            <CircleModule
              width={width}
              minWidth={minWidth}
              top={top}
              right={right}
              titleText={titleText}
              description={description}
              key={i}
            />
          ))}
        </>
      }
      rightMarginTop="0.375em"
    />
  )
}

export default Page
